   */
  export default {
    data() {
      return {
        title: '欢迎各位同事参加',
        content: 'Dear Colleagues and Friends,We are delighted to invite' +
        ' you to the 18th biennial ESID meeting to be held from 24-27 October,' +
        ' 2018, in Lisbon, Portugal. This meeting will be focusing on ' + 'From Fetus to Elderly It is being organized for you by the local and international scientific committee.The ESID 2018 meeting in Lisbon will encompass  the very latest exciting perspectives bridging  basic and clinical research across primary immunodeficiency and related infection, autoimmunity and inflammation. A very stimulating and innovative programme  brings together   a unique  faculty of  internationally  renowned clinicians and scientists to present and discuss significant and exciting developments in diagnostic immunology, genetics and immunobiology of primary immunodeficiencies, through keynote lectures, symposia,' +
