<template>
  <el-row>
    <el-col :span="20" :offset="2">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>注册信息表</span>
        </div>
        <el-form ref="registerForm" :model="registerForm" :rules="rules" :inline="true">
          <el-form-item label="姓 名" prop="userName">
            <el-input v-model="registerForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="单 位" prop="company">
            <el-input v-model="registerForm.company"></el-input>
          </el-form-item>
          <el-form-item label="专 业" prop="profession">
            <el-checkbox-group v-model="registerForm.profession">
              <el-checkbox label="1">普儿</el-checkbox>
              <el-checkbox label="2">风湿免疫</el-checkbox>
              <el-checkbox label="3">感染</el-checkbox>
              <el-checkbox label="4">呼吸</el-checkbox>
              <el-checkbox label="5">血液肿瘤</el-checkbox>
              <el-checkbox label="6">检验</el-checkbox>
              <el-checkbox label="7">科研</el-checkbox>
              <el-checkbox label="8">其他</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="职 称">
            <el-input v-model="registerForm.title"></el-input>
          </el-form-item>
          <el-form-item label="医学毕业年月" prop="timeDate">
            <el-date-picker v-model.trim="registerForm.timeDate" type="date" placeholder="选择日期"
                            :editable="false"></el-date-picker>
          </el-form-item>
          <el-form-item label="地 址" prop="address">
            <el-input v-model="registerForm.address"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="telephone">
            <el-input v-model.trim="registerForm.telephone" placeholder="请输入手机或座机号"></el-input>
          </el-form-item>
          <el-form-item label="摘要主题" prop="abstract">
            <el-input v-model="registerForm.abstract"></el-input>
          </el-form-item>
          <el-form-item label="电子邮箱" prop="email">
            <el-input v-model.trim="registerForm.email" placeholder="请输入电子邮箱"></el-input>
          </el-form-item>
          <div class="registerForm-techar">
            <span>导师信息</span>
          </div>
          <el-form-item label="导师姓名 " prop="tutorName">
            <el-input v-model="registerForm.tutorName"></el-input>
          </el-form-item>
          <el-form-item label="单 位" prop="tutorCompany">
            <el-input v-model="registerForm.tutorCompany"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="telephone">
            <el-input v-model.trim="registerForm.tutorTelephone" placeholder="请输入手机或座机号"></el-input>
          </el-form-item>
          <el-form-item label="电子邮箱" prop="tutorEmail">
            <el-input v-model.trim="registerForm.tutorEmail" placeholder="请输入电子邮箱"></el-input>
          </el-form-item>
        </el-form>
        <div class="registerForm-button">
          <el-button type="success" @click="saveForm('registerForm')">提交信息</el-button>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script type="text/ecmascript-6">
  /**
   * Created by xzc
   * 描述:
   */
  import {registerSave} from '../../http/apiResource'
  export default {
    data() {
      return {
        rules: {
          userName: [
            {required: true, message: '姓名不能为空', trigger: 'blur'}
          ],
          company: [
            {required: true, message: '单位不能为空', trigger: 'blur'}
          ],
          profession: [
            {required: true, message: '专业不能为空', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '电子邮箱不能为空', trigger: 'blur'}
          ],
          timeDate: [
            {required: true, message: '医学毕业年月不能为空', trigger: 'blur'}
          ],
          tutorName: [
            {required: true, message: '导师姓名不能为空', trigger: 'blur'}
          ],
          tutorCompany: [
            {required: true, message: '导师单位不能为空', trigger: 'blur'}
          ]
        },
        registerForm: {
          userName: '',
          company: '',
          profession: [],
          email: '',
          title: '',
          timeDate: '',
          address: '',
          telephone: '',
          abstract: '',
          tutorName: '',
          tutorCompany: '',
          tutorTelephone: '',
          tutorEmail: ''
        }
      }
    },
    methods: {
      saveForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post(registerSave, this.registerForm).then(res => {
              this.userInfo = res.data
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  .registerForm-techar {
    margin: 2rem 5rem;
    width: 80%;
    border-bottom: 0.2rem solid #f15a22;
  }
  .el-card__header .clearfix span {
    font-size: 1.2rem;
  }
  .registerForm-techar {
    display: block;
    line-height: 3rem;
    text-align: left;
    font-size: 1.2rem;
    font-weight: bold;
  }

  .registerForm-button {
    width: 100%;
    text-align: center
  }

</style>



